nav {
    background: rgba(0,0,0,0.3);
    width:max-content;
      height: 4rem;  
    align-items: center;
    display: block;
    padding: 2rem 2rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    bottom: 4rem;
    display: flex;
    gap: 2rem;
    border-radius: 3rem;
    backdrop-filter: blur(15px);
}

nav a {
    background: transparent;
    border-radius: 50%;
    display: flex;
    color :var(--color-light);
    font-size: 1.75rem;

}

nav a :hover {
    background: rgba(0,0,0,0.3);
}

nav a.active {
    background: var(--color-bg);
    color: var(--color-white);
    stroke-width: 0.5rem;
    
    
}


@media screen and (max-width:600px){
    nav{
         
        padding: 0.7rem 1.7rem;
    }

    nav a {
         
        font-size: 1rem;
    
    }

    nav a :hover {
        background: rgba(0,0,0,0.3);
    }
    
    nav a.active {
        background: var(--color-bg);
        color: var(--color-white);
        stroke-width: 0.5rem;
        
        
    }
   
}

