
.event_container {
    

    /* background-color: rgb(15, 26, 36); */
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
    grid-auto-rows: minmax(100px , auto);
    padding-top: 10%;
    

}
 
.event_content {
    /* background-color: aqua; */
    display: grid;
    grid-template-rows: 1fr 1fr;
}

.event_des {
    width: 80%;
    justify-self: center;
    color: var(--color-light);
}

.event_reg{
    width: 60%;
    justify-self: center;
    border-radius: 1rem;
     
}

.event_reg:hover{
    background-color: rgba(75, 107, 126, 0.295);
    
}



.event_pos {
    width: 100%;
    justify-self: center;
}

h3{
    padding-top: 10px;
    padding-left: 50px;
    padding-right: 50px;
}


/*==mediaqueries==*/

@media screen and (max-width:1024px){
     h3 {
         font-size: small;
     }
   
}
@media screen and (max-width:600px){
    .event_container{
        grid-template-columns: 1fr; 
    }

    h3 {
        font-size: medium;
        padding-top: 0;
        padding-left: 5px;
        padding-right: 5px;
    }
   
}