header{
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}
.header__container{
    text-align: center;
    height: 100%;
    position: relative;

}

.cta{
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

.header__socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.header__socials::after{
    content: "";
    width: 1px;
    height: 3rem;
    background: var(--color-primary);
}

 
 

h1 {
    font-size: 200px;
    font-family: 'Bonheur Royale', cursive;
}

#jntu {
     font-size: 30px;
      
    
}

#presents{
    font-family: 'Bonheur Royale';
font-style: normal;
font-weight: 400;
font-size: 30px;


align-items: center;
text-align: center;
letter-spacing: 0.55em;

color: #FFFFFF;


}


 

/*==mediaqueries==*/
@media screen and (max-width:1024px){
    header{
        height: 68vh;
    }
    h1 {
        font-size: 90px;
        font-family: 'Bonheur Royale', cursive;
    }
    #jntu {
        font-size: 25px;
     }
}

 

@media screen and (max-width:600px){
    header{
        height: 100vh;
    }
    .header__socials,
    .scroll__down{
        display: none;
    } 

    h1 {
        font-size: 80px;
        font-family: 'Bonheur Royale', cursive;
    }

    #jntu {
        font-size: 20px;
     }
}

